import axios from 'axios';

const KHOA_API_BASE_URL = "http://localhost:8082/api/khoasv/tutorials";
const KHOAktluat_API_BASE_URL ="http://localhost:8082/api/khoasv/tutorials?title=khoa_kt_luat";
const KHOAktcn_API_BASE_URL ="http://localhost:8082/api/khoasv/tutorials?title=khoa_ktcn";
const KHOAtp_API_BASE_URL ="http://localhost:8082/api/khoasv/tutorials?title=khoa_nn_cntp";
const KHOAsp_API_BASE_URL ="http://localhost:8082/api/khoasv/tutorials?title=khoa_sp_khcb";
class KhoaService {

    getKhoa(){
        return axios.get(KHOA_API_BASE_URL);
    }
    getKhoaktluat(){
        return axios.get(KHOAktluat_API_BASE_URL);
    }
    getKhoaktcn(){
        return axios.get(KHOAktcn_API_BASE_URL);
    }
    getKhoatp(){
        return axios.get(KHOAtp_API_BASE_URL);
    }
    getKhoasp(){
        return axios.get(KHOAsp_API_BASE_URL);
    }
    createKhoa(tutorials){
        return axios.post(KHOA_API_BASE_URL,tutorials);
    }

    getKhoaById(tutorials_Id){
        return axios.get(KHOA_API_BASE_URL + '/' + tutorials_Id);
    }

    updateKhoa(tutorials, tutorials_Id){
        return axios.put(KHOA_API_BASE_URL + '/' + tutorials_Id, tutorials);
    }

    deleteKhoa(tutorials_Id){
        return axios.delete(KHOA_API_BASE_URL + '/' + tutorials_Id);
    }
}

export default new KhoaService()