import React, { Component } from "react";
// =============================Khu vuc Router react dom=============================
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  NavLink,
} from "react-router-dom";
// =============================Khu vuc Router react dom=============================

// =============================Khu vuc UI=============================
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import "antd/dist/antd.css"; // or 'antd/dist/antd.less'

// =============================Khu vuc UI=============================

// =============================Khu vuc thong tin=============================
import Login from "./components/Login/login.component";
import Register from "./components/Register/register.component";
import Profile from "./components/Profiles/profile.component";
import Welcome from "./components/WelCome/home.component";
// =============================Khu vuc thong tin=============================

// =============================Khu vuc user=============================
import BoardUser from "./components/User/board-user.component";
import R from "./components/User/RecipeReviewCard";
// =============================Khu vuc user=============================

// =============================Khu vuc Moder=============================
import BoardModerator from "./components/Mode/board-moderator.component";
// =============================Khu vuc Moder=============================

// =============================Khu vuc Admin=============================
import BoardAdmin from "./components/Admin/board-admin.component";
import ListUser from "./components/Admin/ListUser";
import KhoaAll from "./components/Admin/listkhoa/KhoaAll";
import View from "./components/Admin/listkhoa/View";
import CreateKhoa from "./components/Admin/listkhoa/CreateKhoa";
import Pagi from "./components/User/StickyHeadTable";
import VerticalLinearStepper from "./components/Admin/listkhaosat/VerticalLinearStepper";
import Viewktcn from "./components/WelCome/Viewktcn";
import Viewktluat from "./components/WelCome/Viewktluat";
import Viewcntp from "./components/WelCome/Viewcntp";
import Viewsp from "./components/WelCome/Viewsp";
// =============================Khu vuc Admin=============================

// =============================Khu vuc Frame=============================
import Siderbar from "./components/LayoutComponents/siderbarCom";
import FooterComponent from "./components/LayoutComponents/Footer/FooterComponent";

import Viewsi from "./components/WelCome/Viewsi";
import Navbarhome from "./components/LayoutComponents/Header/Header";
import { Container } from "react-bootstrap";
import About from "./components/LayoutComponents/About/About";
import Contact from "./components/LayoutComponents/Contactus/Contact";
import MessengerCustomerChat from "react-messenger-customer-chat";
import Loadding from "./components/LayoutComponents/Load/Loadding";
// =============================Khu vuc Frame=============================

class App extends Component {
  render() {
    return (
      <Router>
        <Navbarhome />
        <Loadding />
        <div style={{ marginTop: "70px", width: "100%" }}>
          <Switch>
            <Route
              exact
              path={["/", "/welcome", "/home"]}
              component={Welcome}
            />
            <Route exact path="/login" component={Login} />
            <Route
              exact
              path={["/register", "/add-user/:id"]}
              component={Register}
            />
            <Route exact path="/profile" component={Profile} />
            <Route exact path="/about" component={About} />
            <Route exact path="/contact" component={Contact} />
            <Route path="/user" component={BoardUser} />
            <Route path="/mod" component={BoardModerator} />
            <Route path="/admin" component={BoardAdmin} />
            <Route path="/listuser" component={ListUser} />
            <Route path="/listks2" component={VerticalLinearStepper} />

            <Route path="/khoasv" component={KhoaAll}></Route>
            <Route path="/view-khoa/:id" component={View}></Route>
            <Route path="/views/:id" component={Viewsi}></Route>
            <Route path="/add-khoa/:id" component={CreateKhoa}></Route>
            <Route path="/pagi" component={Pagi}></Route>
            <Route path="/S" component={Siderbar} />
            <Route path="/cntp" component={Viewcntp} />
            <Route path="/ktcn" component={Viewktcn} />
            <Route path="/ktluat" component={Viewktluat} />
            <Route path="/supham" component={Viewsp} />
          </Switch>
        </div>
        <FooterComponent />
        <MessengerCustomerChat
          pageId="105088811303071"
          appId="318524673317959"
        />
      </Router>
    );
  }
}

export default App;
