import React, { Component } from "react";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import { Card,Container, Row } from "react-bootstrap";
import AuthService from "../../services/auth.service";
import "./login.css";

const required = value => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        Ãi báº¡n Æ¡i! Äiá»n vÃ o cho ÄÃºng cÃ¡i báº¡n Æ¡i!
      </div>
    );
  }
};

export default class Login extends Component {
  constructor(props) {
    super(props);
    this.handleLogin = this.handleLogin.bind(this);
    this.onChangeUsername = this.onChangeUsername.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);

    this.state = {
      username: "",
      password: "",
      
      loading: false,
      message: ""
    };
  }

  onChangeUsername(e) {
    this.setState({
      username: e.target.value
    });
  }

  onChangePassword(e) {
    this.setState({
      password: e.target.value
    });
  }

  handleLogin(e) {
    e.preventDefault();

    this.setState({
      message: "",
      loading: true
    });

    this.form.validateAll();

    if (this.checkBtn.context._errors.length === 0) {
      AuthService.login(this.state.username, this.state.password).then(
        () => {
          this.props.history.push("/welcome");
          window.location.reload();
        },
        error => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          this.setState({
            loading: false,
            message: resMessage
          });
        }
      );
    } else {
      this.setState({
        loading: false
      });
    }
  }

  render() {
    return (
      <div className="col-md-12" style={{marginBottom:"800px",marginTop:"100px"}}> 
     
        <section className="login-block" style={{width:"100%"}}>
        <div className="container">
          <div className="row">
            <div className="col-md-4 login-sec">
              <h2 className="text-center">ÄÄng Nháº­p</h2>
              <Form
            onSubmit={this.handleLogin}
            ref={c => {
              this.form = c;
            }}
           className="login-form">
                <div className="form-group">
                  <label htmlFor="username" className="text-uppercase">Username</label>
                  <Input
                type="text"
                className="form-control"
                name="username"  placeholder="Nháº­p user"
                value={this.state.username}
                onChange={this.onChangeUsername}
                validations={[required]}
              />
                </div>
                <div className="form-group">
                  <label htmlFor="password" className="text-uppercase">Password</label>
                  <Input
                type="password"
                className="form-control"
                name="password"  placeholder="Nháº­p Pass"
                value={this.state.password}
                onChange={this.onChangePassword}
                validations={[required]}
              />
                </div>
                <div className="form-check">
                  <label className="form-check-label">
                    {/* <input type="checkbox" className="form-check-input" />
                    <small>Remember Me</small> */}
                  </label>
                  <button
                className="btn btn-login float-right"
                disabled={this.state.loading}
              >
                {this.state.loading && (
                  <span className="spinner-border spinner-border-sm"></span>
                )}
                <span>Nháº¥n</span>
              </button>
                  {/* <button type="submit" className="btn btn-login float-right">Submit</button> */}
                </div>
                {this.state.message && (
              <div className="form-group">
                <div className="alert alert-danger" role="alert">
                  {this.state.message}
                </div>
              </div>
            )}
            <CheckButton
              style={{ display: "none" }}
              ref={c => {
                this.checkBtn = c;
              }}
            />
              </Form>
              <div className="copy-text">ÄÆ°á»£c táº¡o <i className="fa fa-heart" /> bá»i <a href="https://github.com/tranhuunghiem99">Tráº§n Há»¯u NghiÃªm </a></div>
            </div>
            <div className="col-md-8 banner-sec">
              <div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
                <ol className="carousel-indicators">
                  <li data-target="#carouselExampleIndicators" data-slide-to={0} className="active" />
                  <li data-target="#carouselExampleIndicators" data-slide-to={1} />
                  {/* <li data-target="#carouselExampleIndicators" data-slide-to={2} /> */}
                </ol>
                <div className="carousel-inner" role="listbox">
                  <div className="carousel-item active">
                    <img className="d-block img-fluid" src="https://static.pexels.com/photos/33972/pexels-photo.jpg" alt="First slide" />
                    <div className="carousel-caption d-none d-md-block">
                      <div className="banner-text">
                        <h2>Kháº£o SÃ¡t CTÄT</h2>
                        <p>hehehe</p>
                      </div>	
                    </div>
                  </div>
                  <div className="carousel-item">
                    <img className="d-block img-fluid" src="https://images.pexels.com/photos/7097/people-coffee-tea-meeting.jpg" alt="First slide" />
                    <div className="carousel-caption d-none d-md-block">
                      <div className="banner-text">
                        <h2>Kháº£o sÃ¡t CÄR</h2>
                        <p>oooo</p>
                      </div>	
                    </div>
                  </div>
         
                </div>	   
              </div>
            </div>
          </div>
        </div></section>

      </div>
    );
  }
}
