import React, { Component } from "react";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import { isEmail } from "validator";
import { Breadcrumbs, InputLabel, Link, Select, Typography } from "@material-ui/core";
import AuthService from "../../services/auth.service";
import { Card } from "react-bootstrap";

const required = value => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!(TrÆ°á»ng nÃ y lÃ  báº¯t buá»c!)
      </div>
    );
  }
};

const email = value => {
  if (!isEmail(value)) {
    return (
      <div className="alert alert-danger" role="alert">
        This is not a valid email.(ÄÃ¢y khÃ´ng pháº£i lÃ  má»t email há»£p lá».)
      </div>
    );
  }
};
const vhoten = value => {
  if (value.length < 3 || value.length > 30) {
    return (
      <div className="alert alert-danger" role="alert">
        The hoten must be between 3 and 30 characters.(TÃªn ngÆ°á»i dÃ¹ng pháº£i cÃ³ tá»« 3 Äáº¿n 30 kÃ½ tá»±.)
      </div>
    );
  }
};

const vusername = value => {
  if (value.length < 3 || value.length > 20) {
    return (
      <div className="alert alert-danger" role="alert">
        The username must be between 3 and 20 characters.(TÃªn ngÆ°á»i dÃ¹ng pháº£i cÃ³ tá»« 3 Äáº¿n 20 kÃ½ tá»±.)
      </div>
    );
  }
};
const vdoituong = value => {
  if (value.length < 3 || value.length > 40) {
    return (
      <div className="alert alert-danger" role="alert">
        The username must be between 3 and 40 characters.( pháº£i cÃ³ tá»« 3 Äáº¿n 40 kÃ½ tá»±.)
      </div>
    );
  }
};
const vpassword = value => {
  if (value.length < 6 || value.length > 40) {
    return (
      <div className="alert alert-danger" role="alert">
        The password must be between 6 and 40 characters.(TÃªn ngÆ°á»i dÃ¹ng pháº£i cÃ³ tá»« 6 Äáº¿n 40 kÃ½ tá»±.)
      </div>
    );
  }
};

export default class Register extends Component {
  constructor(props) {
    super(props);
    this.handleRegister = this.handleRegister.bind(this);
    this.onChangeUsername = this.onChangeUsername.bind(this);
    this.onChangeHoten = this.onChangeHoten.bind(this);
    this.onChangeDoituong = this.onChangeDoituong.bind(this);
    this.onChangeEmail = this.onChangeEmail.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);

    this.state = {
      ho_ten:"",
      username: "",
      doi_tuong:"",
      email: "",
      password: "",
      successful: false,
      message: ""
    };
  }
  onChangeHoten(e) {
    this.setState({
      ho_ten: e.target.value
    });
  }
  onChangeUsername(e) {
    this.setState({
      username: e.target.value
    });
  }
  onChangeDoituong(e) {
    this.setState({
      doi_tuong: e.target.value
    });
  }

  onChangeEmail(e) {
    this.setState({
      email: e.target.value
    });
  }

  onChangePassword(e) {
    this.setState({
      password: e.target.value
    });
  }

  handleRegister(e) {
    e.preventDefault();

    this.setState({
      message: "",
      successful: false
    });

    this.form.validateAll();

    if (this.checkBtn.context._errors.length === 0) {
      AuthService.register(
        this.state.ho_ten,
        this.state.username,
        this.state.doi_tuong,
        this.state.email,
        this.state.password
      ).then(
        response => {
          this.setState({
            message: response.data.message,
            successful: true
          });
        },
        error => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          this.setState({
            successful: false,
            message: resMessage
          });
        }
      );
    }
  }

  render() {
    return (
      <div className="col-md-12">
        
          <Breadcrumbs>
          <Link color="inherit" href="/admin">
            Admin Home
          </Link>
          <Link color="inherit" href="/listuser">
            List User
          </Link>
          <Link color="inherit" href="">
            Add User
          </Link>
         
        </Breadcrumbs>
        <Card>
          <img
            src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
            alt="profile-img"
            className="profile-img-card"
          />

          <Form
            onSubmit={this.handleRegister}
            ref={c => {
              this.form = c;
            }}
          >
            {!this.state.successful && (
              <div>
                <div className="form-group">
                  <label htmlFor="ho_ten">Há» tÃªn</label>
                  <Input
                    type="text"
                    className="form-control"
                    name="ho_ten"  placeholder="Há» VÃ  TÃªn"
                    value={this.state.ho_ten}
                    onChange={this.onChangeHoten}
                    validations={[required, vhoten]}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="username">Username</label>
                  <Input
                    type="text"
                    className="form-control"
                    name="username"  placeholder="User Name"
                    value={this.state.username}
                    onChange={this.onChangeUsername}
                    validations={[required, vusername]}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="doi_tuong">Äá»i tÆ°á»£ng Kháº£o SÃ¡t</label>
                
                  <Select  className="form-control"
                    name="doi_tuong"  placeholder="Chá»n Äá»i tÆ°Æ¡ng"
                    value={this.state.doi_tuong}
                    onChange={this.onChangeDoituong}
                    validations={[required, vdoituong]}
                   
                    >

                  {/* <option aria-label="None" value="Äá»i tÆ°á»£ng kháº£o sÃ¡t" /> */}
                  <option value="MODE">Moderator</option>
                  </Select>
                </div>
                <div className="form-group">
                  <label htmlFor="email">Email</label>
                  <Input
                    type="text"
                    className="form-control"
                    name="email"  placeholder="Email"
                    value={this.state.email}
                    onChange={this.onChangeEmail}
                    validations={[required, email]}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="password">Password</label>
                  <Input
                    type="text"
                    className="form-control"
                    name="password"  placeholder="Password"
                    value={this.state.password}
                    onChange={this.onChangePassword}
                    validations={[required, vpassword]}
                  />
                </div>

                <div className="form-group">
                  <button className="btn btn-primary btn-block">Táº¡o</button>
                </div>
              </div>
            )}

            {this.state.message && (
              <div className="form-group">
                <div
                  className={
                    this.state.successful
                      ? "alert alert-success"
                      : "alert alert-danger"
                  }
                  role="alert"
                >
                  {this.state.message}
                </div>
              </div>
            )}
            <CheckButton
              style={{ display: "none" }}
              ref={c => {
                this.checkBtn = c;
              }}
            />
          </Form>
          </Card>
        </div>
        
    );
  }
}
