import React, { Component } from 'react';
import { Card,Col,Container, Row } from "react-bootstrap";
import KhoaService from"../../../services/KhoaService";
import { makeStyles } from '@material-ui/core/styles';
import LinearProgress from '@material-ui/core/LinearProgress';
import { Breadcrumbs, Link, Typography } from "@material-ui/core";
import Paginations from "./Paginations";



const useStyles = makeStyles((theme) => ({
  root: {
    '& > * + *': {
      marginTop: theme.spacing(2),
    },
  },
}));
export default class KhoaAll extends Component {
  
    constructor(props) {
        super(props);
    
        this.state = {
          khoasv: [],
        };
        this.addKhoa = this.addKhoa.bind(this);
        this.editKhoa = this.editKhoa.bind(this);
        this.deleteKhoa = this.deleteKhoa.bind(this);
      }
    
      deleteKhoa(id) {
        KhoaService.deleteKhoa(id).then((res) => {
          this.setState({
            khoasv: this.state.khoasv.filter(
              (khoasv) => khoasv.id !== id
            ),
          });
        });
      }
      viewKhoa(id) {
        this.props.history.push(`/view-khoa/${id}`);
      }
      editKhoa(id) {
        this.props.history.push(`/add-khoa/${id}`);
      }
    
      componentDidMount() {
        KhoaService.getKhoa().then((res) => {
          this.setState({ khoasv: res.data });
        });
      }
    
      addKhoa() {
        this.props.history.push("/add-khoa/_add");
      }
    
    render() {
        return (
          <Container >
               <Breadcrumbs>
          <Link color="inherit" href="/admin">
            Admin Home
          </Link>
          <Link color="inherit" href="/khoasv">
            Khoa
          </Link>
        </Breadcrumbs>
        <h2 className="text-center">Danh SÃ¡ch Khoa</h2>
        <div className="row">
          <button className="btn btn-primary" onClick={this.addKhoa}>
            {" "}
            ThÃªm Khoa
          </button>
        </div>
        <br></br>
        
          
        <Container>  
        <Row >
          
            {this.state.khoasv.map((khoasv) => (
              <Card key={khoasv.id}  style={{width: "250px", height:"250px", padding:"15px" }} >
                  <Card.Header>{khoasv.id} -- {khoasv.khoa_name}</Card.Header>
                
                
                  <Card.Body>
              <Card.Title className="text-center" style={{fontWeight:"bold"}}>{khoasv.khoa_name}</Card.Title>
            </Card.Body> 
                <td className="text-center">
                  <button
                    onClick={() => this.editKhoa(khoasv.id)}
                    className="btn btn-info"
                  >
                    <span class="lnr lnr-pencil"></span>{" "}
                  </button>
                  <button
                    style={{ marginLeft: "10px" }}
                    onClick={() => this.deleteKhoa(khoasv.id)}
                    className="btn btn-danger"
                  >
                    <span class="lnr lnr-trash"></span>{" "}
                  </button>
                  <button
                    style={{ marginLeft: "10px" }}
                    onClick={() => this.viewKhoa(khoasv.id)}
                    className="btn btn-warning"
                  >
                    <span class="lnr lnr-eye"></span>{" "}
                  </button>
                </td>
               <LinearProgress/>
              </Card>
            ))}

            </Row>
            

            <Paginations/>
            
            </Container>
            
            
         </Container>
      

        );
    }
}

// export default KhoaAll;
