import React, { Component } from 'react'
import KhoaService from '../../../services/KhoaService';
import { Breadcrumbs, Link, Typography } from "@material-ui/core";
class CreateKhoa extends Component {
    constructor(props) {
        super(props)

        this.state = {
            // step 2
            id: this.props.match.params.id,
            khoa_name: '',
            title: '',
            files: '',
            created_at: '',
            updated_at: '',
            
        }
        this.changekhoa_nameHandler = this.changekhoa_nameHandler.bind(this);
        this.changetitleHandler = this.changetitleHandler.bind(this);
        this.changefilesHandler = this.changefilesHandler.bind(this);
        this.changecreated_atHandler = this.changecreated_atHandler.bind(this);
        this.changeupdated_atHandler = this.changeupdated_atHandler.bind(this);

    }

    // step 3
    componentDidMount(){

        // step 4
        if(this.state.id === '_add'){
            return
        }else{
            KhoaService.getKhoaById(this.state.id).then( (res) =>{
                let tutorials = res.data;
                this.setState({
                    khoa_name: tutorials.khoa_name,
                    title: tutorials.title,
                    files: tutorials.files,
                    created_at: tutorials.created_at,
                    updated_at: tutorials.updated_at,
                    
                });
            });
        }        
    }
    saveOrUpdateKhoa = (e) => {
        e.preventDefault();
        let tutorials = {
            khoa_name: this.state.khoa_name,
            title: this.state.title,
            files: this.state.files,
            created_at: this.state.created_at,
            updated_at: this.state.updated_at,
        };
        console.log('tutorials => ' + JSON.stringify(tutorials));

        // step 5
        if(this.state.id === '_add'){
            KhoaService.createKhoa(tutorials).then(res =>{
                this.props.history.push('/khoasv');
            });
        }else{
            KhoaService.updateKhoa(tutorials, this.state.id).then( res => {
                this.props.history.push('/khoasv');
            });
        }
    }
    
    changekhoa_nameHandler= (event) => {
        this.setState({khoa_name: event.target.value});
    }
    changetitleHandler= (event) => {
        this.setState({title: event.target.value});
    }
    changefilesHandler= (event) => {
        this.setState({files: event.target.value});
    }
    changecreated_atHandler= (event) => {
        this.setState({created_at: event.target.value});
    }
    changeupdated_atHandler= (event) => {
        this.setState({updated_at: event.target.value});
    }


    cancel(){
        this.props.history.push('/khoasv');
    }

    getTitle(){
        if(this.state.id === '_add'){
            return <h3 className="text-center">ThÃªm Má»i</h3>
        }else{
            return <h3 className="text-center">Cáº­p Nháº­t</h3>
        }
    }
    render() {
        return (
            <div>
                <br></br>
                   <div className = "container">
                        <div className = "row">
                            <div className = "card col-md-6 offset-md-3 offset-md-3">
                                {
                                    this.getTitle()
                                }
                                <div className = "card-body">
                                    <form>
                                        <div className = "form-group">
                                            <label> TÃªn Khoa: </label>
                                            <input placeholder="TÃªn Khoa" name="khoa_name" className="form-control" 
                                                value={this.state.khoa_name} onChange={this.changekhoa_nameHandler}/>
                                        </div>
                                        {/* <div className = "form-group">
                                            <label> NgÃ y Táº¡o: </label>
                                            <input placeholder="NgÃ y Táº¡o" name="created_at" className="form-control" 
                                                value={this.state.created_at} onChange={this.changecreated_atHandler}/>
                                        </div>
                                        <div className = "form-group">
                                            <label> NgÃ y Sá»­a :{this.state.updated_at} </label>
                                            <input placeholder="NgÃ y sá»­a" name="updated_at" className="form-control" 
                                                value={this.state.updated_at} onChange={this.changeupdated_atHandler}/>
                                        </div> */}
                                        <div className = "form-group">
                                            <label> files: </label>
                                            <input placeholder="Files" name="files" className="form-control" 
                                                value={this.state.files} onChange={this.changefilesHandler}/>
                                        </div>
                                        <div className = "form-group">
                                            <label> Loáº¡i: </label>
                                            <input placeholder="loáº¡i" name="title" className="form-control" 
                                                value={this.state.title} onChange={this.changetitleHandler}/>
                                        </div>
                                        

                                        <button className="btn btn-success" onClick={this.saveOrUpdateKhoa}>Save</button>
                                        <button className="btn btn-danger" onClick={this.cancel.bind(this)} style={{marginLeft: "10px"}}>Cancel</button>
                                    </form>
                                </div>
                            </div>
                        </div>

                   </div>
            </div>
        )
    }
}

export default CreateKhoa
