import React, { Component } from 'react';
import KhoaService from "../../services/KhoaService";
import { makeStyles } from "@material-ui/core/styles";
import LinearProgress from "@material-ui/core/LinearProgress";
import { Typography } from "@material-ui/core";
import { Card, Row } from "react-bootstrap";
class Viewktluat extends Component {
    constructor(props) {
        super(props);

        this.state = {
            khoasv: [],
        };
    }

    viewKhoa(id) {
        this.props.history.push(`/views/${id}`);
    }

    componentDidMount() {
        KhoaService.getKhoaktluat().then((res) => {
            this.setState({ khoasv: res.data });
        });
    }
    render() {
        return (
            <div>
                <Row>

                    {this.state.khoasv.map((khoasv) => (
                        <Card
                            key={khoasv.id}
                            onClick={() => this.viewKhoa(khoasv.id)}
                            style={{
                                width: "250px",
                                height: "250px",
                                padding: "10px",

                            }}
                        >
                            <Card.Header>
                                {khoasv.id} -- {khoasv.khoa_name}
                            </Card.Header>

                            <Card.Body>
                                <Card.Title
                                    className="text-center"
                                    style={{ fontWeight: "bold" }}
                                >
                                    {khoasv.khoa_name}
                                </Card.Title>
                            </Card.Body>

                            <LinearProgress />
                        </Card>
                    ))}
                </Row>
            </div>
        );
    }
}

export default Viewktluat;