import React, { Component } from 'react';
import { Card, Container, Jumbotron, Row } from "react-bootstrap";
import { Breadcrumbs, Link, Typography } from "@material-ui/core";
import KhoaService from "../../../services/KhoaService";
import { Viewer } from '@react-pdf-viewer/core';
// import databasesss from"../";

class View extends Component {
    constructor(props) {
        super(props)

        this.state = {
            id: this.props.match.params.id,
            // dtid: this.props.match.params.dtid,
            khoa: {},
         

        };
    }

    componentDidMount() {
        KhoaService.getKhoaById(this.state.id).then(res => {
            this.setState({ khoa: res.data });
        })
    }

    render() {
        return (
            <Container style={{ marginTop: "100px" }}>
                <Breadcrumbs fixed="top">
                    <Link color="inherit" href="/admin">
                        Admin Home
          </Link>
                    <Link color="inherit" href="/khoasv">
                        Khoa
          </Link>
                    <Link color="inherit" href="/view-khoa">
                        Views
          </Link>
                </Breadcrumbs>
                <br></br>
                <Card>                    <h3 className="text-center">Xem Chi Tiáº¿t</h3>
                    <Jumbotron>                        <Row>                            <label> ID: {this.state.khoa.id}</label>
                        <div> </div>
                    </Row>

                        <Row>
                            <label> TÃªn Khoa: {this.state.khoa.khoa_name}</label>
                            <div> </div>
                        </Row>
                    </Jumbotron>
                    <br></br>
    
    <form className='form-group' >
      <input type="file" className='form-control'
  
      />
     <div className='error-msg'></div>
      <br></br>
      <button className='btn btn-success btn-lg' >
        UPLOAD
      </button>
      <button className='btn btn-success btn-lg' >
        Show
      </button>
    </form>
    <br></br>
    <h4>View PDF</h4>
    <div className='pdf-container'>
  
<Viewer fileUrl="/" />
                  </div>

                </Card>
            </Container>

        );
    }
}

export default View;