import React, { useState, useEffect } from "react";

import "./Templates.css";
import blank from "./forms-blank-googlecolors.png";
import party from "./party_invite.png";
import contact from "./contact.png";
import { useHistory } from "react-router-dom";
import uuid from "react-uuid";
import axios from "axios";

import { Container, Jumbotron, Row, Card } from "react-bootstrap";

function Templates() {
  const history = useHistory();

  function createform() {
    var create_form_id = uuid();
    console.log(create_form_id);

    history.push("/form/" + create_form_id);
    var questions_list = [
      {
        questionText: "Question",
        questionType: "radio",
        options: [{ optionText: "Option 1" }],
        open: true,
        required: false,
      },
    ];

    axios.post(`http://localhost:9000/add_questions/${create_form_id}`, {
      document_name: "untitled_form",
      doc_desc: "Add Description",
      questions: questions_list,
    });
  }
  return (
    <Container fuild>
    <Jumbotron>
      <Row>
        <Card onClick={createform} style={{ width: "200px", height: "200px",padding:"15px" }}>
        
          <img src={blank} style={{ width: "auto", height: "auto" }} />
          <Card.Title className="text-center">Trá»ng</Card.Title>
        </Card>

        <Card onClick={createform} style={{ width: "200px", height: "200px",padding:"15px" }}>
          <img src={contact} style={{ width: "auto", height: "auto" }} />
          <Card.Title className="text-center">Contact</Card.Title>
        </Card>

        <Card onClick={createform} style={{ width: "200px", height: "200px",padding:"15px" }}>
          <img src={party} style={{ width: "auto", height: "auto" }} />
          <Card.Title className="text-center">Há»</Card.Title>
        </Card>
  
      </Row>
    </Jumbotron>
    </Container>
  );
}

export default Templates;
