import React, { Component } from 'react';
import {
  Button,
  NavDropdown,
  Nav,
  Navbar,
  NavbarBrand,
  Card,
  Container,
  Row,

} from "react-bootstrap";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  NavLink,
} from "react-router-dom";
import AuthService from "../../../services/auth.service";
import DropdownItem from "react-bootstrap/esm/DropdownItem";
import NavbarToggle from "react-bootstrap/esm/NavbarToggle";
import NavbarCollapse from "react-bootstrap/esm/NavbarCollapse";
import Avatar from '@material-ui/core/Avatar';

class Header extends Component {
  constructor(props) {
    super(props);
    this.logOut = this.logOut.bind(this);

    this.state = {
      showModeratorBoard: false,
      showAdminBoard: false,
      showUserBoard: false,
      currentUser: undefined,
    };
  }

  componentDidMount() {
    const user = AuthService.getCurrentUser();

    if (user) {
      this.setState({
        currentUser: user,
        showModeratorBoard: user.roles.includes("ROLE_MODERATOR"),
        showAdminBoard: user.roles.includes("ROLE_ADMIN"),
        showUserBoard: user.roles.includes("ROLE_USER"),
      });
    }
  }

  logOut() {
    AuthService.logout();
  }

  render() {
    const { currentUser, showModeratorBoard, showAdminBoard, showUserBoard } =
      this.state;
    return (
     
        <Navbar bg="light" expand="lg" fixed="top"  >

        <NavbarBrand>
          <Link to={"/"} className="navbar-brand">
            TGU
          </Link>
          </NavbarBrand>
              <NavbarToggle aria-controls="basic-navbar-nav" />
        <NavbarCollapse id="basic-navbar-nav">
          <Nav className="mr-auto">
          <NavbarBrand className="active">
          <Link to={"/home"} className="navbar-brand">
            Home
          </Link>
          </NavbarBrand>
          <NavbarBrand>
          <Link to={"/about"} className="nav-link">
                    About
                  </Link>
        </NavbarBrand>
        <NavbarBrand>
          <Link to={"/contact"} className="nav-link">
                    Contact
                  </Link>
        </NavbarBrand>
            {showModeratorBoard && (
              <NavDropdown.Item>
                <NavLink to={"/mod"} className="nav-link">
                  Moderator Board
                </NavLink>
              </NavDropdown.Item>
            )}

            {showAdminBoard && (
              <NavDropdown.Item>
                <Link to={"/admin"} className="btn btn-warning">
                  Admin Board
                </Link>

              </NavDropdown.Item>
            )}

            {showUserBoard && (
              <NavDropdown.Item>
                <NavLink to={"/user"} className="btn btn-warning">
                  Moderator Board
                </NavLink>
              </NavDropdown.Item>
            )}
          </Nav>

          {currentUser ? (
            <div className="navbar-nav ml-auto">
              <li className="nav-item">
                <Link to={"/profile"} className="nav-link">
                <Avatar src="/broken-image.jpg"  title={"TÃ i Khoáº£n:"+ currentUser.username}/>
                </Link>
              </li>
              <li className="nav-item">
                {/* <Button variant="danger"> */}{" "}
                <a href="/login" className="nav-link" onClick={this.logOut}>
                <Button variant="danger">
                  LogOut
                  </Button>
                </a>
                {/* </Button> */}
              </li>
            </div>
          ) : (
            <div className="navbar-nav ml-auto">
              <li className="nav-item">
                <Button variant="primary">
                  <Link to={"/login"} className="nav-link">
                    Login
                  </Link>
                </Button>
              </li>
            </div>
          )}
        </NavbarCollapse>

      </Navbar>

    );
  }
}

export default Header;