import React, { Component } from "react";
import { Jumbotron, Button, Col, ListGroup, Navbar } from "react-bootstrap";
import { SidebarContent } from "react-pro-sidebar";
import { Redirect } from "react-router-dom";
import AuthService from "../../services/auth.service";
import UserService from "../../services/user.service";
import KhoaService from "../../services/KhoaService";
import { makeStyles } from "@material-ui/core/styles";
import LinearProgress from "@material-ui/core/LinearProgress";
import { Box, Breadcrumbs, Chip, Link, Paper, Tab, Tabs, Typography } from "@material-ui/core";
import { Card, Container, Row, Carousel } from "react-bootstrap";
class ViewALL extends Component {
    constructor(props) {
        super(props);

        this.state = {
            khoasv: [],
        };
    }

    viewKhoa(id) {
        this.props.history.push(`/views/${id}`);
    }

    componentDidMount() {
        KhoaService.getKhoa().then((res) => {
            this.setState({ khoasv: res.data });
        });
    }

    render() {
        return (
            <div>
                <Row>

                    {this.state.khoasv.map((khoasv) => (
                        <Card
                            key={khoasv.id}
                            // onClick={() => this.viewKhoa(khoasv.id)}
                            style={{
                                width: "250px",
                                height: "250px",
                                padding: "10px",

                            }}
                        >
                            <Card.Header>
                                {khoasv.id} -- {khoasv.khoa_name}
                            </Card.Header>

                            <Card.Body>
                                <Card.Title
                                    className="text-center"
                                    style={{ fontWeight: "bold" }}
                                >
                                    {khoasv.khoa_name}
                                </Card.Title>
                            </Card.Body>

                            <LinearProgress />
                            <button
                    style={{ marginLeft: "10px" }}
                    onClick={() => this.viewKhoa(khoasv.id)}
                    className="btn btn-warning"
                  >
                    <span class="lnr lnr-eye"></span>{" "}
                  </button>
                        </Card>
                    ))}

                </Row>
            </div>
        );
    }
}

export default ViewALL;