import React, { Component } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import UserService from "../../services/user.service";
import { Breadcrumbs, Link, Typography } from "@material-ui/core";
import { SidebarContent } from "react-pro-sidebar";
import { Button } from "react-bootstrap";

class ListUser extends Component {
  constructor(props) {
    super(props);

    this.state = {
      user: [],
    };
    this.addUser = this.addUser.bind(this);
    this.deleteUser = this.deleteUser.bind(this);
  }
  addUser() {
    this.props.history.push("/add-user/_add");
  }
  deleteUser(id) {
    UserService.deleteUser(id).then((res) => {
      this.setState({
        user: this.state.user.filter(
          (user) => user.id !== id
        ),
      });
    });
  }
  componentDidMount() {
    UserService.getAllUser().then((res) => {
      this.setState({ user: res.data, roles: res.data });
    });
  }
  render() {
   
    
    return (
      <p style={{marginTop:"100px"}}>
        <Breadcrumbs >
          <Link color="inherit" href="/admin">
            Admin Home
          </Link>
          <Link color="inherit" href="/listuser">
            List User
          </Link>
        </Breadcrumbs>
<SidebarContent>
<button className="btn btn-primary" onClick={this.addUser}>
            {" "}
            ThÃªm User
          </button>
        
        <Paper>
          <TableContainer>
            <Table stickyHeader aria-label="sticky table">
              <TableHead>
                <TableRow>
                  <TableCell>ID</TableCell>
                  <TableCell>Há»TÃªn</TableCell>
                  <TableCell>Username </TableCell>
                  <TableCell>Email</TableCell>
                  <TableCell>ÄTKS</TableCell>
                  <TableCell>Action</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
              
                {this.state.user.map((user) => (
                  <TableRow hover role="checkbox" tabIndex={-1}>
                    <TableCell>{user.id}</TableCell>
                    <TableCell>{user.ho_ten}</TableCell>
                    <TableCell>{user.username}</TableCell>
                    <TableCell>{user.email}</TableCell>
                    <TableCell>{user.doi_tuong}</TableCell>
                    <TableCell>Sá»­a | <button
                    style={{ marginLeft: "10px" }}
                    onClick={() => this.deleteUser(user.id)}
                    className="btn btn-danger"
                  >
                    <span class="lnr lnr-trash"></span>{" "}
                  </button></TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
          <TablePagination rowsPerPageOptions={[10, 25, 100]} component="div" />
        </Paper>
        </SidebarContent>
      </p>
    );
  }
}

export default ListUser;
