import React, { Component } from "react";
import { Navbar, Row, NavbarBrand, Container, Tab, Col, Nav } from 'react-bootstrap';
import Viewalls from "./ViewAll";
import Viewcntp from './Viewcntp';
import Viewktcn from './Viewktcn';
import Viewktluat from './Viewktluat';
import Viewsp from './Viewsp';

class Navbarclick extends Component {
    render() {
        return (
            <div>
                <Tab.Container defaultActiveKey="All">
                    <Row>
                        <Col sm={3}>
                            <Nav variant="pills" className="flex-column">
                                <Nav.Item >
                                    <Nav.Link eventKey="All">
                                        All
                            </Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="khoa_kt_luat">Khoa KT Luáº­t</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="khoa_ktcn">Khoa KTCN</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="khoa_sp">Khoa SP</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="khoa_cntp">Khoa CNTP</Nav.Link>
                                </Nav.Item>
                            </Nav>
                        </Col>
                        <Col sm={9}>
                            <Tab.Content>
                                <Tab.Pane eventKey="All">
                                <Viewalls/>
                                </Tab.Pane>
                                <Tab.Pane eventKey="khoa_kt_luat">
                                    <Viewktluat/>
                                </Tab.Pane>
                                <Tab.Pane eventKey="khoa_ktcn">
                                    <Viewktcn/>
                                </Tab.Pane>
                                <Tab.Pane eventKey="khoa_sp">
                                    <Viewsp/>
                                </Tab.Pane>
                                <Tab.Pane eventKey="khoa_cntp">
                                    <Viewcntp/>
                                </Tab.Pane>
                            </Tab.Content>
                        </Col>
                    </Row>
                </Tab.Container>
            </div>
        );
    }
}

export default Navbarclick;